import React from 'react';
import Card from './Card';


    const CardList = ({monsters}) => {
        return (
            <React.StrictMode>
           {
            monsters.map((user,i) => {
                return (<Card 
                key={i} 
                id={monsters[i].id} 
                name={monsters[i].name} 
                email={monsters[i].email} 
                />
                )
             })
           }
            </React.StrictMode>
    );
}

export default CardList;