import React, {Component} from 'react';
import CardList from '../componenets/CardList';
import SearchBox from '../componenets/SearchBox';
import './App.css';
import ErrorBoundry from '../componenets/ErrorBoundry';
import Scrool from '../componenets/Scrool';


class App extends Component {
    constructor() {
        super();
        this.state = {
            monsters: [],
            searchfield:''
        }
    }
        componentDidMount() {
            fetch('https://jsonplaceholder.typicode.com/users')
            .then(response=> response.json())
            .then(users => this.setState({monsters: users}));
  
        }

        onSearchChange = (event)=>{
            this.setState({searchfield:event.target.value})
           
        }


    render(){
      
        const filterMonsters = this.state.monsters.filter(monster =>
            { return monster.name.toLowerCase().includes(this.state.searchfield.toLowerCase())
                })
                    return (
                        <React.StrictMode>
                        <div className="tc">
                            <h1 className="f1">MonsterFriends</h1>
                            <SearchBox searchChange={this.onSearchChange}/>
                            <Scrool>
                                <ErrorBoundry>
                                    <CardList monsters={filterMonsters}/>
                                </ErrorBoundry>
                            </Scrool>
                         </div>
                         </React.StrictMode>
                    );
                }
            }




export default App;